<template>
  <div v-for="(a, i) in blog" :key="i">
    <!-- 페이지 이동시켜주는 함수
    $route는 현재경로
    $router는 페이지 이동관련 기능
    - $router.go(-1): 뒤로가기
    - $router.go(1): 앞페이지로가기 -->
    <h5 @click="$router.push(`/detail/${i}`)">{{blog[i].title}}</h5>
    <p>{{blog[i].date}}</p>
  </div>
</template>

<script>
export default {
    name: 'List',
    props: {
        blog: Array,
    },
}
</script>

